body {
  margin: 0;
  font-family: Roboto, Helvetica, Arial, sans-serif;
}

#reminder-app {
  .panel-container {
    display: flex;
    flex-direction: column;

    margin-left: auto;
    margin-right: auto;

    height: 100vh;
    max-width: 720px;

    // when screen width <= 480px
    @media (max-width: 480px) {
      width: 100%;
    }

    .panel-content {
      padding: 10px;
      flex-grow: 1;
      overflow: scroll;
    }
  }

  .add-float-button {
    position: absolute;
    right: 30px;
    bottom: 70px;
  }

  .unauth-buttons {
    display: flex;
    justify-content: space-around;
    width: 100%;

    button {
      width: 140px;
    }
  }
}
