#reminder-app {
  .product-item-amount {
    display: flex;
    align-items: baseline;

    .amount {
      margin-right: 15px;
      color: gray;
    }
  }

  // mobile screen size
  @media only screen and (max-width: 400px) {
    .product-item-text > * {
      width: 150px;
    }

    .product-item-avatar {
      display: none;
    }
  }
}
